import React, { Component, memo } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/free-mode';
import 'swiper/css/mousewheel';
import './scss/ReelShop.scss'

import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'
import { isTagShop } from '../../../actions/api'

class ReelShop extends Component {
  constructor(props) {
    super(props)
    this.sliderHeight = React.createRef();
    this.inputRefs = {};
  }
  state = {
    sliderHeight: ''
  }

  requestData = (swiper) => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps, loaderData } = this.props;
    if (swiper.progress > 0.8 && hasMoreData && !loaderData.isShowMoreLoading) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {
    if (this.sliderHeight.current) {
      this.setState({ sliderHeight: this.sliderHeight.current.clientHeight - 40 });
    }
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData } = this.props;

    if (postData && postData.length == 1) {

      let arrow_left_id = document.querySelector(".tb_rtp_arrow_left__")
      let arrow_right_id = document.querySelector(".tb_rtp_arrow_right__")
      if (arrow_left_id && arrow_right_id) {
        document.querySelector(".tb_rtp_arrow_left__").style = 'display:none'
        document.querySelector(".tb_rtp_arrow_right__").style = 'display:none'
      }

    }
  }



  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, parentID, keyRender, containerSize, onClickPopUpSlider } = this.props;

    const inputName = parentID;
    const { sliderHeight } = this.state;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const trimContent = wall.Personalization.trimcontent === 1 ? true : false;
    const minCardSize = 240;
    const cardCount = ((containerSize.width / minCardSize).toFixed())
    let responsiveSize = (cardCount == 1 ? 2 : (cardCount)) - 0.4
    var newData = postData
    if (responsiveSize > postData.length) responsiveSize = postData.length
    const responsivePerView = numberOfCoumn == 0 ? responsiveSize : parseInt(numberOfCoumn) - 0.4
    if (responsivePerView && Math.round(responsivePerView) <= postData.length) newData = Array(2).fill(postData).flat()
    return <>{parentID != "" ?
      <div className="tb_rtp_post_container" ref={this.sliderHeight}>
        <Swiper
          key={keyRender}
          ref={(parentID) => this.inputRefs[inputName] = parentID}
          style={{ width: '100%', height: '100%' }}
          onScroll={(swiper) => this.requestData(swiper)}
          onSlideChange={(swiper) => this.requestData(swiper)}
          onAutoplay={(swiper) => this.requestData(swiper)}
          autoHeight={true}
          speed={700}
          edgeSwipeDetection={true}
          slidesPerView={responsivePerView}
          touchRatio={2}
          cssMode={isMobile}
          passiveListeners={true}
          height={'100%'}
          freeMode={{ enabled: true, sticky: false, momentumBounceRatio: 2, momentumVelocityRatio: 3 }}
          autoplay={isTagShop ? { delay: slidePost === 1 ? slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true, } : false}
          loop={!hasMoreData}
          slideFullyVisibleClass="tb-swiper-slide-fully-visible"
          breakpoints={{
            0: {
              slidesPerView: mobileColumn == 0 ? responsiveSize : parseInt(mobileColumn) - 0.4
            },
            480: {
              slidesPerView: mobileColumn == 0 ? responsiveSize : parseInt(mobileColumn) - 0.4
            },
            575: {
              slidesPerView: mobileColumn == 0 ? responsiveSize : parseInt(mobileColumn) - 0.4
            },
            767: {
              slidesPerView: responsivePerView
            },
            1024: {
              slidesPerView: responsivePerView
            },
            1400: {
              slidesPerView: responsivePerView
            },
            1600: {
              slidesPerView: responsivePerView
            }
          }}
          mousewheel={{ forceToAxis: true, sensitivity: 2 }}
          watchSlidesProgress={true}
          modules={[Autoplay, Mousewheel, FreeMode]} className={`tb_rtp_post_slider ${trimContent ? 'tb_rtp_fixed_height' : ''}`}>
          <>
            {
              ((newData && newData.length > 0)) && newData.map((item, index) => {
                const cardData = completeDataObject[item];
                return (isTagShop || (cardData.type === 3 || cardData.type === 5)) ? <SwiperSlide style={{ margin: 0, padding: 0, }} key={index} p_id={parentID} className={`tb_${cardData.id}`} >
                  {({ isVisible }) => <Card isVisible={isVisible} ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} wall={wall} sliderHeight={sliderHeight} onClickPopUpSlider={onClickPopUpSlider} />}
                </SwiperSlide> : null
              })}
          </>
        </Swiper>
      </div> : null}</>
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(ReelShop));