import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import { isTagShop } from "../../../../actions/api";
const Media = ({ itemData, wallID, wall, ownerId, preload, isVisible }) => {
  const isVideo = (itemData.type === 3 || itemData.type === 5) && (wall.ThemeRule.autoPlay == 1 || !isTagShop) ? true : false
  return <div className="tb_rtp_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">
    {isVideo ?
      <Video VideoClass="tb_rtp_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={178} isCover={true} muted={true} />
      :
      <Image ImageClass="tb_rtp_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={178} />
    }
  </div>
}
export default Media