import React, { Component } from 'react';
import ProductImage from '../../../Components/PostPopUp/Elements/ProductImage';
import ReactHtmlParser from 'react-html-parser';
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel, Navigation, Pagination } from 'swiper/modules';
import 'swiper/css';

const CalculateDiscount = (originalPrice, discountedPrice) => {
       if (originalPrice != 0) {
              if (discountedPrice > 0 && discountedPrice < originalPrice) {
                     let discountAmount = originalPrice - discountedPrice;
                     let discountPercentage = (discountAmount / originalPrice) * 100;
                     return discountPercentage > 0 ? `${discountPercentage.toFixed()}%` : '';

              } else {
                     return false
              }
       } else {
              return false
       }
}
const Products = ({ itemData, wall }) => {
       const ugc_products = itemData.ugc_products
       const { Price, ProductTitle } = wall.ProductSetting
       const isMultipleProduct = ugc_products && ugc_products.length == 1 ? true : false
       return <div className="tb_rtp_wrap" id={`slide-${itemData.id}`}>
              <div className="tb_rtp_wrap_in">
                     <Swiper
                            id={`product_slide-${itemData.id}`}
                            modules={[Pagination, Navigation]}
                            spaceBetween={10}
                            navigation={{
                                   prevEl: `#tb_rtn_prev-${itemData.id}`,
                                   nextEl: `#tb_rtn_next-${itemData.id}`,
                            }}
                     >
                            {ugc_products.map((product, index) => <SwiperSlide key={index}><div className='tb_rtp' key={index}>
                                   <div className='tb_rtp_media' style={{ opacity: product.UgcProduct.product_image ? 1 : 0 }}>
                                          <ProductImage ImageClass="tb_rtp_img" media={product.UgcProduct.product_image} size={false} itemDetails={''} />
                                   </div>
                                   <div className='tb_rtp_title_w'>
                                          {(product.UgcProduct.product_title) ?
                                                 <div className={`tb_rtp_title tb-cTBfont-${ProductTitle.varient}`}>{ReactHtmlParser(product.UgcProduct.product_title)}</div>
                                                 : null}
                                   </div>
                                   {product.UgcProduct.product_price > 0 || product.UgcProduct.product_discount > 0 ?
                                          <div className="tb_rtp_price_wrap">
                                                 <div className={`tb_rtp_price tb-cTBfont-${Price.varient}`}>
                                                        {product.UgcProduct.price_currency_symbol}{product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price}
                                                 </div>
                                                 {product.UgcProduct.product_discount > 0 && product.UgcProduct.product_discount != product.UgcProduct.product_price ? <div className={`tb_rtp_d_price tb-cTBfont-${Price.varient}`}>{product.UgcProduct.price_currency_symbol}{product.UgcProduct.product_price}</div> : null}


                                                 {CalculateDiscount(product.UgcProduct.product_price, product.UgcProduct.product_discount) && CalculateDiscount(product.UgcProduct.product_price, product.UgcProduct.product_discount) != '' ? <div className='tb_rtp_dp'>{CalculateDiscount(product.UgcProduct.product_price, product.UgcProduct.product_discount)}</div> : null}
                                          </div>
                                          : null}
                            </div>
                            </SwiperSlide>)}
                     </Swiper>
                     {!isMultipleProduct ?
                            <div className={`tb_rtn_wrap`} onClick={(e) => { e.stopPropagation() }}>
                                   <div id={`tb_rtn_prev-${itemData.id}`} className={`tb_rtn_btn tb_rtn_btn_left tb__icon tb-arrow-left-alt`}><div></div></div>
                                   <div id={`tb_rtn_next-${itemData.id}`} className={`tb_rtn_btn tb_rtn_btn_right tb__icon tb-arrow-right-alt`}><div></div></div>
                            </div>
                            : null}
              </div>
       </div>
}
export default Products;