import React, { PureComponent } from "react";
import Media from "./Media";
import { themeImpressionTracking, kFormatter } from "../../../../utils";
import { isTagShop, isTagEmbed, suiteTrack } from "../../../../actions/api";
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
import Products from "./Products";
import AuthorDetails from "./AuthorDetails";
import ViewCount from "../../Elements/ViewCount";


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    const { itemData, itemIndex, onClickPopUpSlider } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.mediaHeight)
    if (itemData.isPopUp) {
      onClickPopUpSlider(itemIndex, itemData)
    }
  }

  render() {
    const { itemData, itemIndex, wallID, ownerId, wall, isVisible, clickToShowPopUp, ThemeRule, personalization, sliderHeight } = this.props;
    const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
    const cardStyle = { backgroundColor: ThemeRule.cardColor, minHeight: sliderHeight };
    const isShopIcon = isTagShop && (isProduct && itemData.hotspot === 0) ? true : false
    const isHotspot = isTagShop && itemData.hotspot && isProduct ? true : false
    const isVideo = (itemData.type === 3 || itemData.type === 5) ? true : false

    return <div item-id={itemData.id} id={`tb-hc-post-${itemData.id}`} className="tb_rtp_post_wrapper" ref={this.mediaHeight} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} style={{ paddingLeft: wall.Personalization.padding / 2, paddingRight: wall.Personalization.padding / 2 }} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0}>
      <div className={`tb_rtp_post_in tb_icon_animate ${!isTagShop ? 'tb_rtp_post_in_animate' : ''}`} style={cardStyle} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
        <div className="tb_rtp_post_media_wrapp" role="button" tabindex="0" aria-label="Pop Up Button">
          {isVideo && ThemeRule.showReelsView == 1 && itemData?.views > 0 ? <ViewCount count={itemData?.views} /> : null}
          <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} preload={itemIndex == 0 ? true : false} isVisible={isVisible} key={`img${itemIndex}_${itemData.id}`} />
          {!isTagShop ?
            <div className="tb_rtp_play_ico">
              <div className="tb__icon tb-play"><div></div></div>
            </div>
            : null}
        </div>

        {isProduct ?
          <Products itemData={itemData} wall={wall} key={`product_slide${itemData.id}`} />
          :
          <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} wall={wall} />
        }
      </div>
    </div>
  }
}
