import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font, mediaType, ownerId, wall }) => {
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const { ProductTitle } = wall.ProductSetting
  return (
    <div className="tb_rtp_wrap">
      <div className="tb_rtp_wrap_in" style={{ marginTop: !postAuthor ? 0 : '' }}>
        {postAuthor ? <div className='tb_rtp_media'><Author network={network} author={author} authorClass={'tb_rtp_a_img'} key={author.username} /> </div> : ''}
        {postAuthor ? <div className='tb_rtp_title_w'>
        <div className={`tb_rtp_authorname tb-cTBfont-${ProductTitle.varient}`}>{author.username ? author.username.length > 0 ? `${author.username}` : `` : ''}</div>
        </div> : ''}
        {postTime ? <Time postTime={postTime} timeClass={`tb_rtp_time tb-cTBfont-${ThemeRule.font_varient}`} /> : ''}
      </div>
    </div>
  );
}
export default AuthorDetails